import React from 'react';
import { NavLink } from 'react-router-dom';

const Navbar = () => {
    return (
        <> 
        <div className="container-fluid">
            <div className='row'>
                <div className='col-12 mx-auto'>
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                <NavLink className="navbar-brand" to="/" style={{fontSize:'30px', color:'#565387'}}><b>Home</b></NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                
                <li className="nav-item">
                <NavLink className="nav-link" to="/service"><b>Service</b></NavLink>
                </li>
                <li className="nav-item">
                <NavLink className="nav-link " to="/about" ><b>About</b></NavLink>
                </li>
                <li className="nav-item">
                <NavLink className="nav-link" to="/contact"><b>Contact</b></NavLink>
                </li>
                </ul>
                </div>
                </div>
                </nav>
                </div>
            </div>
        </div>
        </>
    )
}

export default Navbar;